



















import { Vue, Component } from 'vue-property-decorator'
import { getLiff } from '@/plugins/liff'

@Component
export default class App extends Vue {
  liff: any = {}
  profile: any = {}

  created () {
    this.initial()
  }

  async initial () {
    const liff = this.liff = await getLiff('1653390945')
    this.profile = await liff.getProfile()
  }
}
